// src/app/(dashboard)/dashboard/page.jsx
"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Upload, CheckCircle } from "lucide-react";

export default function DashboardPage() {
  const [file, setFile] = useState(null);
  const [isDragging, setIsDragging] = useState(false);

  // Handle file selection
  const handleFileChange = (event) => {
    const selectedFile = event.target.files[0];
    if (
      selectedFile &&
      (selectedFile.type === "application/pdf" ||
        selectedFile.type ===
          "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
        selectedFile.type === "application/msword")
    ) {
      setFile(selectedFile);
    } else {
      alert("Please upload a valid PDF or DOC file.");
      setFile(null);
    }
  };

  // Handle drag-and-drop events
  const handleDragOver = (event) => {
    event.preventDefault();
    setIsDragging(true);
  };

  const handleDragLeave = () => {
    setIsDragging(false);
  };

  const handleDrop = (event) => {
    event.preventDefault();
    setIsDragging(false);
    const droppedFile = event.dataTransfer.files[0];
    if (
      droppedFile &&
      (droppedFile.type === "application/pdf" ||
        droppedFile.type ===
          "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
        droppedFile.type === "application/msword")
    ) {
      setFile(droppedFile);
    } else {
      alert("Please drop a valid PDF or DOC file.");
      setFile(null);
    }
  };

  // Handle form submission
  const handleSubmit = (event) => {
    event.preventDefault();
    if (file) {
      console.log("File submitted:", file.name);
      // Add API calls or further processing here
    } else {
      alert("Please select a file to upload.");
    }
  };

  return (
    <div className="max-w-[100vw] overflow-x-hidden flex flex-col items-center justify-center min-h-screen py-12">
      {/* Title outside the card */}
      <h1 className="text-6xl font-extrabold mb-12 leading-tight text-white drop-shadow-lg text-center">
        <span className="text-red-700">Syllabus</span>{" "}
        <span className="text-black">Extraction</span>
      </h1>

      <Card className="w-full max-w-2xl mx-auto bg-white border border-gray-200 shadow-md rounded-2xl">
        <CardHeader className="pb-4">
          <p className="text-lg text-center text-gray-600">
            Upload your syllabus in PDF or DOC format to get started
          </p>
        </CardHeader>
        <CardContent className="p-8">
          <form onSubmit={handleSubmit} className="space-y-8">
            <div
              className={`flex flex-col items-center justify-center w-full h-80 border-2 border-dashed rounded-xl cursor-pointer transition-colors duration-200 ${
                isDragging
                  ? "border-red-400 bg-red-50"
                  : "border-gray-300 hover:bg-gray-50"
              }`}
              onDragOver={handleDragOver}
              onDragLeave={handleDragLeave}
              onDrop={handleDrop}
            >
              <label
                htmlFor="file-upload"
                className="flex flex-col items-center justify-center w-full h-full"
              >
                <div className="flex flex-col items-center space-y-4">
                  {file ? (
                    <CheckCircle className="w-16 h-16 text-green-500" />
                  ) : (
                    <Upload className="w-16 h-16 text-gray-400" />
                  )}
                  <p className="text-lg font-medium text-gray-700 uppercase">
                    {file ? file.name : "Upload PDF or DOC file"}
                  </p>
                  <p className="text-sm text-gray-500">
                    Click to browse or drag and drop your file here
                  </p>
                </div>
                <input
                  id="file-upload"
                  type="file"
                  accept=".pdf,.doc,.docx"
                  onChange={handleFileChange}
                  className="hidden"
                />
              </label>
            </div>
            <Button
              type="submit"
              className="w-full bg-[#F28C82] hover:bg-[#F28C82]/90 text-white text-lg py-6 rounded-lg transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed uppercase"
              disabled={!file}
            >
              Extract Syllabus
            </Button>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}